import React from "react";

class Detail extends React.Component {
    componentDidMount() {
        const {location, history} = this.props
        if (location.state === undefined) {
            history.push("/");
        }
    }

    render() {
        const {location} = this.props;
        if (location.state) {
            const youtube_link = `https://www.youtube.com/embed/${location.state.yt_trailer_code}`;
            return (
                <div>
                    <iframe width="783" height="587" src={youtube_link} frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                    <span>{location.state.title}</span>
                </div>
            )
        } else {
            return null;
        }
        
    }
}

export default Detail;